.Key {
    text-align: center;
    padding: 20px;
  }
  
  h1 {
    font-size: 2.5rem;
    margin-bottom: 20px;
  }
  
  .product-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    margin-top: 20px;
  }
  
  .product-card {
    border: 1px solid #ddd;
    border-radius: 8px;
    border-color:purple;
    padding: 20px;
    text-align: center;
  }
  
  .product-card img {
    width: 300px;
    height: 300px;
    border-radius: 2px;
    object-fit: contain;
  }
  
  .product-card h2 {
    font-size: 1.25rem;
    margin: 10px 0;
  }
  
  .product-card p {
    font-size: 1rem;
    color: #555;
  }